<template>
    <div class="image-container">
      <div class="content">
        <div class="button-container">
            <button @click="scaleUp">Zoom In</button>&nbsp;&nbsp;&nbsp;&nbsp;
            <button @click="scaleDown">Zoom Out</button>
      </div>
      </div>
      <div 
        @mousedown="startMoving" 
        @mousemove="move" 
        @mouseup="endMoving"
        @mouseleave="endMoving"
        ref="container"
      >
        <img 
          :src="src" 
          :style="{ transform: `translate(${x}px, ${y}px) scale(${scale})`, }" 
          @contextmenu.prevent
          @dragstart.prevent
          style="position: absolute; top: 0; left: 0; z-index: 1;"
          ref="image"
        />
        <slot></slot>
      </div>
    </div>
  </template>
  
  <script>
  import { ref, onMounted } from 'vue';
  
  export default {
    props: ['src'],
    setup() {
      const x = ref(0);
      const y = ref(0);
      const moving = ref(false);
      const image = ref(null);
      const container = ref(null);
      const scale = ref(1);
  
      const startMoving = () => {
        moving.value = true;
      };
  
      const move = (e) => {
        if (!moving.value) return;
        x.value += e.movementX;
        y.value += e.movementY;
      };
  
      const endMoving = () => {
        moving.value = false;
      };
  
      const scaleUp = () => {
        scale.value += 0.1;
      };
  
      const scaleDown = () => {
        if (scale.value > 0.1) {
          scale.value -= 0.1;
        }
      };
  
      onMounted(() => {
        const imageWidth = image.value.clientWidth;
        const imageHeight = image.value.clientHeight;
        const containerWidth = container.value.clientWidth;
        const containerHeight = container.value.clientHeight;
  
        x.value = (containerWidth - imageWidth) / 2;
        y.value = (containerHeight - imageHeight) / 2;
      });
  
      return { x, y, startMoving, move, endMoving, image, container, scale, scaleUp, scaleDown };
    },
  };
  </script>
  
  <style scoped>
  .image-container {
    width: 800px;
    height: 600px;
    position: relative;
    overflow: hidden;
  }

  .button-container {
  margin-bottom: 20px;
}
.content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

  </style>
  